.item{
    width: 200px;
     
}
 
.item p{
    margin: 6px 0px;
    padding-left: 10px;
}
.item-prices{
    display: flex;

    
}
.item-old-price{
    
    color: rgb(121, 118, 118);
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
     
   
}
.item-new-price{
    padding-left: 10px;
     display: flex;
    font-size: 18px;
    font-weight: 500;
    margin-right:30px ;
    padding-bottom: 10px;
    
    
}
.item:hover{
    transform: scale(1.05);
    transition: 0.7s;
    box-shadow:  5px 5px 10px 10px  #e7eaf6;
}