.product-page
{
    width: 100%;
     height: 100%;
    position: absolute;
    transform: translate(-50%, -50%) ;
    left: 50%;
    display: contents;
     top: 40%;
     background-position: center;
     background-size: cover;
     margin-bottom: 40px;
}
.product-page .product-left{
    width: 50%;
    margin-left: 40px; 
}
.product-main-img img{
    width: 500px ;
    border-radius: 30px;
    margin: 30px 10px 10px 50px;
}

.product-right
{
width: calc(100% - 58%);
position: absolute;
top: 30%;
right: 0;
height: 100%;
 
}  
.product-right h1
{
    display:flex;
    position: relative;
    font-size: 50px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    padding: 3px;
    transform: translateX(-5%);
}

.product-btn h2{
    display: flex;
    font-size: 20px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
     margin-top: 10px;
     transform: translateX(-3%);
}
.product-btn button{
    width: 50px;
    margin-top: 20px;
   padding: 5px;
    margin-right: 5px;
    text-align: center;
    align-items: center;
    cursor: pointer;
    background-color: transparent;
    border:  2px  solid black;
    transform: translateX(-303%);
}
.price{
    margin-top: 10px;
    display:flex;
    flex-direction: column;
    margin-bottom: 10px;
   
    
}
.price h5{
    display: flex;
    color:#5c5470 ;
    text-decoration: line-through;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
      margin-top: 10px;
}

.price h4{
  
    position: absolute;
    left: 10%;
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 10px;
}
.product-right p{
  
 width: 70%;
 display: flex;
 
 flex-wrap: nowrap;
 line-height: 1.3;
 text-wrap: wrap;
 align-items: center;
 
}
.cart-btn{
     width: 130px;
     padding: 10px;
      margin-top: 30px;
      border-radius: 5px;
      outline: none;
      border: none;
      transform: translateX(-170%);
      cursor: pointer;
}
.cart-btn:hover{
     transition: 0.4s;
     background-color: #ff6f3c;
     color: #e3e3e3;
 
}


