.product {
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20;
    margin-bottom:  50px;
    margin-right: 0;
    
}
.product h1{
    color: #260c1a;
    font-size: 50px;
    font-weight: 500;
    font-family: 'Salsa', cursive;
}
.product-list {
    width: 90%;
    margin-top: 50px;
    display: flex;
    gap: 50px;   
    justify-content:space-between;
    
}
.product-list img{
    margin: 10px;
    display: flex;
    height: 30vh;
    margin-bottom: 20px;
} 