.cart{
    height:100%;
    width: 100%;
    margin-bottom: 50px;
    display: flex;

}
.about-cart{
    width: 100%;
    margin-top: 30px;

}
.about-cart h5{
    margin-bottom: 50px;    
    font-size: 30px;
     color:  #393e46;
}
.about-cart table{
    width:  50%;
   /* background-color: greenyellow; */
    justify-content: center;
    align-items: center;
    margin-left: 25%;
    text-align: center;
    box-shadow:#ececec 3px 3px 5px 2px;
}

.table td{
    align-items: center;
    margin: 0;
   /* background-color: green;
   */

}
.table td img{
    margin: 5px;
    border-radius: 5px;

}
.table tr button{
    align-items: center;
    justify-content: center;
    margin-top: 40px;
   padding: 10px;
   margin-left: 5px;
   border-radius: 5px;
   background-color: transparent;
   outline: none;
    cursor: pointer;
   
}
.table tr button:hover{
  background-color: orangered;
  transition: 0.3s;
}
.total{
    width: 20%;
    margin-top: 40px;
    align-items: center;
    justify-content: center;
    text-align: right;
    margin-left: 55%;
   
  
    
}
.total  h3{
    font-size: 15px;
    font-weight: 400;
    text-decoration:underline;
}
.total  button{
    margin-top: 20px;
    width: 150px;
    border-radius: 5px;
    padding: 10px;
    outline: none;
    cursor: pointer;
    background-color:#ff9a3c ;
    color: white;
    border: none;


}
.total  button:hover{
   box-shadow: 2px 3px 2px 3px #e7eaf6;   
   /* transform: translateZ(10px); */
   color: black;
   transition: 0.4s;

}