.tshirt {
    
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.tshirt h1{
    margin-bottom:  30px;
    color: #260c1a;
    font-size: 50px;
    font-weight: 500;
    font-family: 'Salsa', cursive;
}
 
.tshirt-collection {
    width: 90%;
    margin-left: 8%;
    display: grid;
    gap: 30px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
.tshirt-collection img{
    margin: 10px;
    width: 90%;
    display: flex;
    height: 30vh;
}