.footer{
    width: 100%;
    margin-top: auto;
    box-shadow: 5px 5px 5px 5px #e0ebeb;
    height: 40vh;
    display: flex;
    position: sticky;
    
}

.footer-logo {
    align-items: center;
    display: flex;
}
.footer-logo img{
    margin-top: 30px;
    margin-left: 20px;
    width: 80px;
    border:1px solid black ;
    box-shadow: 2px 2px 2px 2px #e3e3e3;
    border-radius: 30px;
    
}
.footer-logo p{
    margin-top: 30px;
     margin-left: 10px;
    font-size: 20px;
    outline: invert;
    font-family: 'Lemon', serif;
    font-weight: 500;
  color: #ff6f3c;
   
}
.footer-list ul{
  display: flex;
  gap: 30px;
    cursor: pointer;
     margin-top: 30px;
     display: flexbox;
     list-style: none;
     margin-left: 100px;
  
}
 
.footer-list li{

  padding: 3px;
}
.footer-social {
  display: inline-flex;
  align-items: center;
  margin-left: 80px;
}
 
 .footer-container img{
  width: 40%;
  border: none;
  cursor: pointer;
  border-radius: 30px;
 }
 .copyright p{
  color: black;
  font-size: 15px;
  margin-left: 60px;
  font-weight: 700;
 }