.wrapper{
     width: 100%;
     height: 550px;
     position: absolute;
     transform: translate(-50%, -50%) ;
     left: 50%;
     top: 64%;
  background-image: url('../Assets/wall.jpg');
  background-position: center;
  background-size: cover;
}
.wrapper .signup-con{
  width: 58%;
  top: 0;
  left: 0;
  position: absolute;

}

 .signup-con h1{
  font-size: 50px;
  align-items: center;
  justify-content: center;
  display: flex;
  margin-top: 22%;
  color: #00204a;
  font-weight: 900;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
 
}

.signup-con h3{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 70px; 
  font-size: 20px;
  font-weight: 300;
  color: #dbd8e3;
}
.signup-con h3 span{
 color: white;
 margin: 10px;
}
  .signup-con h2{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  color: #dbd8e3;
  font-weight: 200;
}
.socialmedia  {
 display: flex;
 align-items: center;
 justify-content: center;

  }

 .signup-area{
width: calc(100% - 58%);
position: absolute;
top: 0;
right: 0;
height: 100%;
background-color: blur(10%);
backdrop-filter: blur(10px);
 
}
.formlogin h1{
  display: flex;
  text-align: center;
  
  justify-content: center;
  margin-top: 80px;
}
 .wrapper .icon-signpage{
  margin-left: 5px;
  color:  white;
}
.wrapper .socialmedia a{
  color: black;
  margin: 10px;
  font-size: 20px;
}
 .wrapper .input-box{
 position: relative;
 width: 100%;
 height: 40px;
border: none;
margin: 30px 0;
display: flex;
align-items: center;
justify-content: center;
 
}
.input-box input{
width: 60%;
height: 30px;
border-radius: 40px;
border: none;
background: transparent;
border:2px solid #79c2d0;
padding: 5px;
padding-left: 20px;
font-size: 16px;
 
}
.input-box input::placeholder{
color:  hsl(217, 10%, 25%);
 padding: 10px;
 font-weight: 300;
 font-size: 20px;
}
.input-box .page-icon{
  position: absolute;
  right: 120px;
  top: 30%;
  color: #5585b5;
}

.rules{
  display: flex;
  justify-content: space-evenly ;
}
.rules a{
 
  color: white;
}
.rules label{
  color: #a2a8d3;
}

.wrapper button{
  margin-top: 30px;
  width: 30%;
  font-size: 18px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  border: none;
  outline: none;
  margin-left: 35%;
  color: hsl(210, 39%, 52%);
  height: 40px;

}
.wrapper button:hover{
 background-color: #5585b5;
 color: white;
 transition: .7s;
}
















