
.letter{
    margin-top: 50px;
    width: 850px;  
    height: 40vh;
    background: linear-gradient(87.4deg, rgb(255, 241, 165) 1.9%, rgb(200, 125, 76) 49.7%, rgb(83, 54, 54) 100.5%);
    align-items: center;
    display: flex;
    margin-left: 20%;
    margin-bottom: 20px;
    flex-direction: column;
    justify-content: center;
}
.letter h1{
    display: flex;
    font-size: 30px;
    font-weight: 200;
    font-family: 'Playfair Display', serif;
    margin-bottom: 10px;
}
.letter span{
    margin-left: 10px;
    color: #ffe9e3;
    font-weight: 300;

}
.letter p{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 200;
    letter-spacing: 1.1px;
    margin-bottom: 10px;
}
.letter-input {
    justify-content: center;
    top: 10px;
    display: flex;

}
.letter-input input{
  flex: 1;
  outline: none;
  border: 1px solid #ececec;
  border-radius: 10px 0 0 10px;
  padding: 10px;
  align-items: center;
  
}
.letter-input button{
  flex: 1;
  cursor: pointer;
  outline: none;
  border: 2px solid #ececec;
  border-radius: 0 10px 10px 0;
  background-color: transparent;
  padding: 10px;
  color: #fff;  
  background: linear-gradient(90deg, rgba(235,242,242,1) 0%, rgba(159,74,33,1) 55%, rgba(2,0,36,1) 100%);
  transition: 0.3s;

 
}
.letter-input button:hover{
background-position-x: left;
 background:left; 
 color: black;
}


 