.home{
   
    height: 100vh;
    display: flex;
}
.home-left{
    display: flex;
    flex-direction: column ;
    justify-content: center;
    padding-left: 170px;
       
}
.home-left h3{
    font-size: 35px;
    color:  #702283;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 600;
    text-align: center;
}
.home-left h4{
    font-family: 'Dancing Script', cursive;
    font-family: 'Noto Serif', serif;
    font-weight: 500;
    font-size: 20px;

}
.home-left h5{
    font-family: 'Dancing Script', cursive;
font-family: 'Noto Serif', serif;
font-weight: 500;
}

.home-left img{
    width: 100px;
    height: 5vh;
    margin-left: 75px;
    
}
.home-btn button{
    margin: 0;
    margin-top: 20px;
    width: 100px;
    padding: 8px;
    border-radius: 20px;
    border: transparent;
    cursor: pointer;
    font-family: 'Noto Serif', serif;
    font-weight: 500;
    

}
.home-left button:hover{
    background-color: #702283;
    color: wheat;
    transform: 0.7s;
    box-shadow: 4px 4px 4px 3px #430f58;
}
 .home-right {
    display: flex;
    flex-direction: column;
    flex: 1;
 
 }
 .home-right img{
   width: 80%;
   margin: 4;
   padding-left: 180px;
   height: 80vh;
 }