.Anime-Shoes{
    width: 100%;
    
}
.Anime-Shoes h1{
 display: flex;
 justify-content: center;
 align-items: center;
 font-size: 40px;
 font-family: 'Dancing Script', cursive;
 color: #393e46; 
 font-weight: 600;
 
}
.Collection {
   display: grid;
   align-items: center;
   justify-content: center;
   grid-template-columns: 1fr 1fr 1fr 1fr;
   margin-top: 50px;
   gap: 20px;
   margin-left: 7%;
}
.Collection img{
   margin: 10px;
    width: 90%;
    display: flex;
    height: 30vh;
}