.navbar{
      display: flex;
      padding: 16px;
      width: auto;
      justify-content: space-between;
      height: 80px;
      background-color:  #ff9a3c; ;
}
.nav-logo{
    width: 80px;
    display: flex;
    border: 2px solid #e7eaf6;
    border-radius: 50%;
    gap: 20px;
}
.nav-logo p{
 display: flex;
 align-items: center;
 justify-content: space-evenly;
 font-size: 20px;
 font-weight: 200;
 font-family: 'Lemon', serif;
 color:  #e7eaf6;
}
.nav-menu{ 
     display: flex;
    align-items: center;
    list-style: none;
    color: #e7eaf6;
     gap: 50px;
    justify-content: center;
    
}
.nav-menu hr{
    border: none;
    width: 70%;
    height: 2px;
    border-radius: 10px;
    background-color: #f96d00;
}
.nav-menu ul{
    display: flex;
   gap: 40px;
   float: inline-start;
}

.nav-menu li{
    display: flex;
    float: left;
     flex-direction: column;
    align-items: center;
     justify-content: center;
     cursor: pointer;
     gap: 3px;
}
.nav-login {
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login button {
    width: 80px;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    height: 50px;
    border-radius: 75px;
    border: #e7eaf6;
    gap: 30px;
    
}
.nav-login button:hover{
background-color: #ececec;
transition: 0.3s;
color: #ff9a3c;
}
.nav-login img{
   justify-content: space-between;
    cursor: pointer;
   
}
.nav-cart-count{
   width: 22px;
   height: 22px;
   display: flex;
   justify-content: center;
   align-items: center;
   margin-top: -35px;
   margin-left: -55px ;
   font-size: 13px;
   border-radius: 11px;
   background-color: rebeccapurple;

}
.nav-cart-count::after{
    background-color: #ececec;
    color: #ececec;
}