.banner{
    width: 950px;
    height: 50vh;
    background: linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%);
    align-items: center;
    justify-content: space-around;
    margin-left: 180px;
    display: flex;
    box-shadow:  2px 2px 5px 5px #fcfefe;
    margin-bottom: 50px;
    margin-top: 50px;
    box-shadow: 5px 5px 10px 10px #fdfcfb;

    
}
.banner-left h4{
    font-family: 'Playfair Display', serif;
    color: #680747;
    font-size: 40px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 10px;
  
}
.banner-left h3{
    font-family: 'Pacifico', cursive;
    font-family: 'Playfair Display', serif;  
   font-weight: 500;
   margin-bottom: 10px;
   
}
.banner-left button{
    
    border-radius: 50px;
    border: transparent;
    margin: 10px;
    padding: 15px;
    margin-left: -10px;
    letter-spacing: 1px;
     
}
.banner-left button:hover{
   box-shadow:  #680747 0px 7px 30px 0px;
    background-color: #680747;
    color: white;
    transition: 0.6s;
    
}
.banner-left button:active{
    box-shadow:  #680747 0px 7px 30px 0px;
    background-color: #680747;
    color: white;
    
   
}

.banner-right img{
    display: flex;
    width:   150px;

}
 
 
 